import { Row, Col } from "reactstrap";
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter';
import { materialOceanic } from 'react-syntax-highlighter/dist/esm/styles/prism';
import { Link } from "react-router-dom";
// import dotenv from 'react-syntax-highlighter/dist/esm/languages/prism/dotenv';
// SyntaxHighlighter.registerLanguage('dotenv', dotenv);

export default function Start() {
    return<>
        <Row className="mt-4">
            <Col md="1"/>
            <Col md="8"> 
                <h1 className="font-sans-serif" style={{fontWeight: "700"}}>Primeiros Passos</h1>
                <br></br><br></br>


                <h3 className="font-sans-serif" style={{fontWeight: "700"}}>Pr-requisitos</h3>
                <hr></hr>


                <p className="font-sans-serif">
                    Para executar uma instncia do CRUD-Generator  necessrio ter instalado o <a href="https://nodejs.org/en" target="_blank">Node.js</a>.
                </p> 
                

                <br></br>

                <h3 className="font-sans-serif" style={{fontWeight: "700"}}>Onde obter o cdigo fonte?</h3>
                <hr></hr>

                <p className="font-sans-serif">
                    Para obter o cdigo fonte do CRUD-Generator voc pode baixar ou clonar o repositrio dele no GitHub <a href="https://github.com/gpacianotto/CRUD-Generator" target="_blank">aqui</a>.
                </p>
                
                <br></br>

                <h3 className="font-sans-serif" style={{fontWeight: "700"}}>Instalar Dependncias</h3>
                <hr></hr>

                <p className="font-sans-serif">
                    Para que o CRUD-Generator rode em seu computador,  necessrio instalar algumas dependncias que o projeto utiliza para funcionar. Para isso, abra um terminal na pasta raz do projeto e rode os seguintes comandos:
                </p>

                <SyntaxHighlighter language="shell" >
                    {`npm install`}
                </SyntaxHighlighter>

                <br></br>

                <h3 className="font-sans-serif" style={{fontWeight: "700"}}>DotEnv</h3>
                <hr></hr>

                <p className="font-sans-serif">
                    Para que o CRUD-Generator rode em seu computador,  necessrio criar um arquivo '.env' na raz do projeto. Veja <Link to="/dotenv"> nesta seo</Link> quais variveis voc deve definir neste arquivo.
                </p>

                

            </Col>
            <Col md="3"/>
        
        </Row>
    
    
    </>
}