import { Row, Col } from "reactstrap";
import DataBaseExampleImage from '../images/database-example1.png'

export default function Intro() {
    return<>
        <Row className="mt-4">
            <Col md="1"/>
            <Col md="8"> 
                <h1 className="font-sans-serif" style={{fontWeight: "700"}}>Introduo</h1>
                <br></br><br></br>


                <h3 className="font-sans-serif" style={{fontWeight: "700"}}>O que  CRUD-Generator?</h3>
                <hr></hr>

                <p className="font-sans-serif">
                    CRUD-Generator  uma aplicao backend, open-source, construda no modelo API REST, cuja principal funcionalidade  criar e manipular abstraes de sistemas. 
                    
                </p>

                <p className="font-sans-serif">
                    Cada abstrao criada no CRUD-Generator permite gerar tabelas no banco de dados principal relacionando cada tabela com sua respectiva abstrao.
                </p>


                <div className="text-center">
                    <img src={DataBaseExampleImage} style={{width: "100%", height: "100%"}}></img>
                </div>
                

                <p className="font-sans-serif">

                    Em outras palavras, CRUD-Generator fragmenta seu prprio banco de dados em bancos de dados menores. Cada fragmento pertence a um sistema cadastrado no CRUD-Generator.
                </p>

                <br></br>

                <h3 className="font-sans-serif" style={{fontWeight: "700"}}>CRUD-Generator  um Backend as a Service?</h3>
                <hr></hr>

                <p className="font-sans-serif">
                    CRUD-Generator funciona exatamente como um Backend as a Service, porm com uma diferena. Backends as a Service so sistemas que rodam em servidores web, enquanto que o CRUD-Generator foi projetado para rodar na mquina do desenvolvedor.
                </p>
                <p className="font-sans-serif">
                    Portanto, este software tem como objetivo ser uma ferramenta que auxilia na prototipao de interfaces frontend, bem como auxiliar na criao de esboos de aplicaes backend.
                </p>
                <br></br>
                <h3 className="font-sans-serif" style={{fontWeight: "700", color: "red"}}>ATENO!</h3>
                <hr></hr>

                <p className="font-sans-serif">
                    NO  RECOMENDADO subir instncias do CRUD-Generator para ser utilizado em servidores web sem ajustes prvios. Este software no foi criado para rodar em servidores, foi feito para ser uma ferramenta de testes locais, portanto pode conter falhas de segurana. No nos responsabilizamos por qualquer uso indevido da aplicao.
                </p>

            </Col>
            <Col md="3"/>
        
        </Row>
    
    
    </>
}