import { Row, Col } from "reactstrap";
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter';
import { materialOceanic } from 'react-syntax-highlighter/dist/esm/styles/prism';
import { Link } from "react-router-dom";
import Screenshot1 from '../images/screenshot-1.png'
// import dotenv from 'react-syntax-highlighter/dist/esm/languages/prism/dotenv';
// SyntaxHighlighter.registerLanguage('dotenv', dotenv);

export default function DotEnv() {
    return<>
        <Row className="mt-4">
            <Col md="1"/>
            <Col md="8"> 
                <h1 className="font-sans-serif" style={{fontWeight: "700"}}>Variveis de Ambiente</h1>
                <br></br><br></br>

                <p className="font-sans-serif">
                    Nesta pgina esto descritas todas as variveis de ambiente que devem ser definidas em um arquivo <mark><i>'.env'</i></mark> na raz do projeto para que o CRUD-Generator funcione corretamente.
                </p>

                <div className="text-center mb-4">
                    <img src={Screenshot1} style={{width: "700px"}}></img>
                </div>

                <h3 className="font-sans-serif" style={{fontWeight: "700"}}>1. DB_USERNAME</h3>
                <hr></hr>

                <SyntaxHighlighter language="dot" >
                    {`DB_USERNAME='root'`}
                </SyntaxHighlighter>
                <p className="font-sans-serif">
                    Essa varivel de ambiente remete ao seu nome de usurio cadastrado no seu SGBD.
                </p>

                <br></br>

                <h3 className="font-sans-serif" style={{fontWeight: "700"}}>2. DB_PASSWORD</h3>
                <hr></hr>

                <SyntaxHighlighter language="dot" >
                    {`DB_PASSWORD='password123'`}
                </SyntaxHighlighter>
                <p className="font-sans-serif">
                    Essa varivel de ambiente remete  sua senha cadastrada no seu SGBD.
                </p>

                <br></br>

                <h3 className="font-sans-serif" style={{fontWeight: "700"}}>3. DB_NAME</h3>
                <hr></hr>

                <SyntaxHighlighter language="dot" >
                    {`DB_NAME='webSystem1'`}
                </SyntaxHighlighter>
                <p className="font-sans-serif">
                    Essa varivel de ambiente remete ao nome do banco de dados que voc criou em seu SGBD.
                </p>

                <br></br>

                <h3 className="font-sans-serif" style={{fontWeight: "700"}}>4. DB_HOST</h3>
                <hr></hr>

                <SyntaxHighlighter language="dot" >
                    {`DB_HOST='localhost'`}
                </SyntaxHighlighter>
                <p className="font-sans-serif">
                Essa varivel de ambiente especifica o endereo do host onde o seu banco de dados est sendo executado. Geralmente, o valor padro  'localhost', o que significa que o banco de dados est sendo executado na mesma mquina que o seu servidor. No entanto, se voc estiver usando um servio de hospedagem de banco de dados ou se o seu banco de dados estiver em outra mquina na rede, voc precisar alterar o valor dessa varivel para apontar para o host correto.
                </p>

                <br></br>

                <h3 className="font-sans-serif" style={{fontWeight: "700"}}>5. HASH_SALT_ROUNDS</h3>
                <hr></hr>

                <SyntaxHighlighter language="dot" >
                    {`HASH_SALT_ROUNDS='10'`}
                </SyntaxHighlighter>
                <p className="font-sans-serif">
                Essa varivel de ambiente determina um nmero inteiro que definir qual a complexidade do algoritmo que ir criptografar as senhas cadastradas no seu sistema. Quanto maior a complexidade, mais seguro, mas tambm maior ser o processamento necessrio. O valor padro  '10'. 
                
                <br/> <br/> <b>Ateno:</b> coloque este valor sempre com aspas simples, mesmo sendo ele um valor inteiro.
                </p>

                <br></br>

                <h3 className="font-sans-serif" style={{fontWeight: "700"}}>6. APP_PORT</h3>
                <hr></hr>

                <SyntaxHighlighter language="dot" >
                    {`APP_PORT=3344`}
                </SyntaxHighlighter>

                <p className="font-sans-serif">
                Essa varivel de ambiente determina qual porta o CRUD Generator ir estar disponvel em seu computador para requisies HTTP
                
                <br/> <br/> <b>Ateno:</b> coloque este valor sempre com aspas simples, mesmo sendo ele um valor inteiro.
                </p>

                <br></br>

                <h3 className="font-sans-serif" style={{fontWeight: "700"}}>7. ROOT_SYSTEM_ID</h3>
                <hr></hr>

                <SyntaxHighlighter language="dot" >
                    {`ROOT_SYSTEM_ID='1'`}
                </SyntaxHighlighter>

                <b>Ateno:</b> coloque o valor sempre com aspas simples, mesmo sendo ele um valor inteiro.
                <br/><br/>

                <p className="font-sans-serif">
                Essa varivel de ambiente especifica qual ser o nmero identificador do sistema root (que  uma abstrao do Painel de Controle do CRUD-Generator). Recomenda-se que, aps sua definio, o nmero identificador no seja mais alterado, pois este ID ser sempre utilizado para realizar operaes com o sistema raiz.<br/><br/>
                
                

                <p>Caso seja necessria a mudana voc precisar: <br/><br/>

                    <ol>
                        <li>
                            Deletar seu banco de dados inteiro (isso acarretar a perda de todos os dados j inseridos).
                        </li>

                        <li>
                            Criar um banco de dados vazio com o mesmo nome definido em <mark><i>DB_NAME</i></mark> e iniciar a aplicao novamente.
                        </li>
                        
                    </ol>

                </p>

                <br/> 
                </p>

                <br></br>

            </Col>
            <Col md="3"/>
        
        </Row>
    
    
    </>
}